*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

body{
    background: #333;
}

.techs{
    display: flex;
    min-height: 350px;
    overflow-x: scroll;
}

.techs::-webkit-scrollbar {
    width: 50px;
    height: 15px;
}

.techs::-webkit-scrollbar-thumb:horizontal {
    border-radius: 15px;
    background: #1b1b1b;
}